<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td><code v-pre="">'https://a.tile.openstreetmap.org'</code></td>
<td><code v-pre="">optional</code>The OpenStreetMap server url.</td>
</tr>
<tr>
<td>fileExtension</td>
<td>String</td>
<td><code v-pre="">'png'</code></td>
<td><code v-pre="">required</code>The file extension for images on the server.</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The rectangle of the layer. <strong>structure: { west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code>The minimum level-of-detail supported by the imagery provider.</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code>The maximum level-of-detail supported by the imagery provider, or undefined if there is no limit.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>The ellipsoid. If not specified, the WGS84 ellipsoid is used.</td>
</tr>
<tr>
<td>credit</td>
<td>String| Object</td>
<td><code v-pre="">'MapQuest, Open Street Map and contributors, CC--bY--sA'</code></td>
<td><code v-pre="">optional</code>A credit for the data source, which is displayed on the canvas.</td>
</tr>
</tbody>
</table>
